import request from './utils/request'
import axios from 'axios'
import { notification } from 'antd'

const { NODE_ENV } = process.env
export const wrapUrl = url =>
  NODE_ENV === 'development'
    ? `http://localhost:3000/${url}`
    : `https://www.sevenrabbit.cn/${url}`
const url = wrapUrl('gen-order')
const genOrder = async body => {
  try {
    const data = await request(`${url}`, { method: 'POST', body })
    return data
  } catch (err) {
    console.log(err)
  }
}
const getWord = async pkg => {
  pkg.forEach((p, i) => {
    setTimeout(() => {
      global.location.assign(`${url}/word?url=${p.path}&filename=${p.filename}`)
    }, 300 * i)
  })
}

const downloadSheet = async time => {
  const jwt = await localStorage.getItem('jwt')
  try {
    const { data } = await axios.get(`${url}/sheet`, { params: { time, jwt } })
    // axios.get(`${url}/download-sheet`, { params: { url: data.url } })
    global.location.assign(`${url}/download-sheet?url=${data.url}`)
  } catch (err) {
    const e = err.response && err.response.data
    if (e) {
      notification.error({
        message: e.error,
        description: e.message
      })
    }
  }
}

const downloadPriceSheet = async time => {
  const jwt = await localStorage.getItem('jwt')
  try {
    const { data } = await axios.get(`${url}/price-sheet`, {
      params: { time, jwt }
    })
    // axios.get(`${url}/download-sheet`, { params: { url: data.url } })
    global.location.assign(`${url}/download-price-sheet?url=${data.url}`)
  } catch (err) {
    const e = err.response && err.response.data
    if (e) {
      notification.error({
        message: e.error,
        description: e.message
      })
    }
  }
}

export { genOrder, getWord, downloadSheet, downloadPriceSheet }
