import React, { useState } from 'react'
import { DatePicker } from 'antd'
import locale from 'antd/es/date-picker/locale/zh_CN'
import './GetSheet.css'
import download from './imgs/download.svg'

const { RangePicker } = DatePicker

export const GetSheet = ({ title, handleDownload }) => {
  const [time, setTime] = useState()
  const onChange = (_, t) => {
    setTime(t)
  }
  const onClick = () => {
    handleDownload(time)
  }
  return (
    <div className="getSheet-btn-block">
      <h3 class="small-block-title">{title}</h3>
      <RangePicker
        locale={locale}
        format="YYYY-MM-DD"
        placeholder={['起', '止']}
        onChange={onChange}
      />
      <img className="icon" src={download} alt="" onClick={onClick} />
    </div>
  )
}
